<template>
  <vx-card>
    <div class="demo-alignment">
      <vs-input type="date" label="วัน/เดือน/ปี" v-model="date" />
      <vs-select
        autocomplete
        class="selectExample"
        label="รายการ"
        v-model="name_select"
      >
        <vs-select-item
          :key="index"
          :value="item.value"
          :text="item.text"
          v-for="(item, index) in options1"
        />
      </vs-select>
      <vs-input type="number" label="จำนวน" v-model="quantity" />
      <vs-input type="number" label="ราคา/ชิ้น" v-model="price" />

      <vs-button color="primary" size="large" @click="insertproduct()"
        >เพิ่ม</vs-button
      >
    </div>
    <br />
    <!-- <vs-button color="success" @click="printInvoice" size="medium">เพิ่ม</vs-button> -->
    <div class="right"></div>

    <vs-table search>
      <template slot="thead">
        <vs-th sort-key="id">ลำดับ</vs-th>
        <vs-th sort-key="date">วันที่</vs-th>
        <vs-th sort-key="slip">เลขที่ใบสั่ง</vs-th>
        <vs-th sort-key="total">ยอดรวม</vs-th>
        <vs-th sort-key="status">สถานะ</vs-th>
        <vs-th></vs-th>
      </template>

      <template slot-scope="{ data }">
        <vs-tr :key="products" v-for="(tr, products) in products">
          <vs-td :data="data[products].id">{{ data[products].id }}</vs-td>

          <!-- <vs-td :data="data[products].date">{{ data[products].date }}</vs-td>

          <vs-td :data="data[products].slip">{{ data[products].slip }}</vs-td>

          <vs-td :data="data[products].total">{{ data[products].total }}</vs-td>

          <vs-td :data="data[products].status">{{ data[products].status }}</vs-td> -->
          <router-link to="/product/orderReceive/detail">
            <vs-icon icon="settings" size="medium" color="primary"></vs-icon>
          </router-link>
          <router-link to="/">
            <vs-icon icon="delete" size="medium" color="primary"></vs-icon>
          </router-link>
        </vs-tr>
      </template>
    </vs-table>
    <vs-table></vs-table>
    <vx-card>
      <vs-table search>
        <thead>
          <tr>
            <th scope="col">รหัสสินค้า</th>
            <th scope="col">ชื่อ</th>
            <th scope="col">จำนวน</th>
            <th scope="col">ราคา</th>
            <th scope="col">ราคารวม</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in products" v-bind:key="product">
            <td>{{ product.code }}</td>
            <td>{{ product.name }}</td>
            <td>{{ product.quantity }}</td>
            <td>{{ product.price }}</td>
            <td>{{ product.quantity*product.price }}</td>
          </tr>
        </tbody>
      </vs-table>
    </vx-card>
  </vx-card>
</template>

<script>
export default {
  data () {
    return {
      products: null,
      name_select: '',
      quantity: null,
      price: null,
      options1: [
        { text: 'IT', value: 0 },
        { text: 'Blade Runner', value: 1 },
        { text: 'Thor Ragnarok', value: 2 }
      ],
      date: ''

    }
  },
  methods: {
    insertproduct () {
      this.$http
        .post('http://api.iseki.test/api/product/', {
          date:this.date,
          quantity:this.quantity,
          price:this.price,
          name_select:this.name_select
        })
        .then((response) => (this.message = response.data.json))
    }
  },
  async mounted () {
    try {
      const { data } = await this.$http.get(
        'http://api.iseki.test/api/product'
      )
      this.products = data.data

    } catch (error) {
      throw error
    }
  }
}
</script>
<style  scoped>
.right {
  text-align: right;
}
</style>
